.aim-trainer {
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
  background-color: #f0f0f0;
}

.score {
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: 24px;
}

.sensitivity {
  position: absolute;
  top: 10px;
  right: 10px;
}

.target {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: red;
  position: absolute;
  transform: translate(-50%, -50%);
  cursor: pointer;
}

.crosshair {
  width: 20px;
  height: 20px;
  border: 2px solid black;
  border-radius: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  pointer-events: none;
}